import React from 'react';




const title = 'Title';
const text = 'text';



export function Modal(title, text, setShow) {
    return (
        <>
         <div style={displayBlock}>
              <div style={modalbox}>
              <div style={textBox}>
              <img style={imgD} src={img} alt="success"/>
              <p style={titleEelm}>{title}</p>
              {text}
              </div>
              <button style={closeOne}  onClick={() => setShow(false)}>
                  X
              </button>
            </div>
        </div>
         </>
    );
};